import React from 'react';
//import axios from 'axios';
import unsplashapi from '../api/apiconfig';

import './App.css';
import SearchBar from './SearchBar';
import ImageList from './ImageList';

class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      images: []
    };
  }
  handleSearchSubmit = async (searchTerm) => {
    //console.log(searchTerm);
    const response = await unsplashapi.get('/search/photos', 
                                    {
                                      params: {query:  searchTerm},
                                    
                                    });
                                                          //.then((response)=>console.log(response.data))
    // 'https://api.unsplash.com/search/photos', {params: {query:  searchTerm},
    // Authorization: 'Client-ID 20e7ac3f9b7a8ebc83d4ae563c5e05bcd25aa6c17ad03470c0c1f41f1d405c0f'}

    //console.log(response.data.results);
    this.setState({images: response.data.results})
  }
  render (){
    return (
      <div className="ui container" style={{margin: '15px'}}>
        <SearchBar onSubmit={this.handleSearchSubmit}/>
        <ImageList images={this.state.images} />
      </div>
    );  
  }
}
