import React from 'react';

export default class SearchBar extends React.Component {
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.onSubmit(this.state.searchTerm); 
        //console.log(this.state.searchTerm);
    }
    // this is an example of uncontrolled form element
    
    // handleChange(e){
    //     console.log(e.target.value);
    // }

    // controlled form element we define state
    state = {
        searchTerm: ''
    }
    render() {
        return (
            <div className="ui segment">
                <form className="ui form" onSubmit={this.handleSubmit}>
                    <div className="field">
                    <label>Image Search:</label>
                    {/* uncontrolled input */}
                    {/* <input type="search" name="SearchField" className="ui field" onChange={this.handleChange} /> */}
                    {/* controlled input */}
                    <input type="search" name="SearchField" className="ui field" value={this.state.searchTerm} onChange={(e) => this.setState({searchTerm: e.target.value})} />

                    </div>
                </form>                
            </div>
        )
    }
}
